import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { Button, CardMedia, CardContent, CardActions, CardActionArea, Card, Typography, Paper } from '@material-ui/core';
import { listWeatherAPI } from '../app/api/app_api';

class App extends Component {
  constructor(props) {
    super(props)

    this.state = {
      weatherResponse: [],
      city: 'Jakarta',
      error: '',
      initialLoading: true,
    }
  }

  async componentDidMount() {
    this.getWeather()
  }

  async getWeather() {
    try {
      const listWeathers = await listWeatherAPI(this.state.city)
      if (listWeathers.status !== 200) throw Error()
      this.setState({ weatherResponse: listWeathers.data })
    } catch (e) {
      this.setState({ error: 'Terjadi kesalahan pada saat memuat data' })
    }
    this.setState({ initialLoading: false })
  }

  render() {
    console.log('test', this.state.weatherResponse)
    const { classes } = this.props;
    return (
      <Card className={classes.card}>
        <CardActionArea>
          <CardMedia
            component="img"
            alt="Contemplative Reptile"
            className={classes.media}
            height="140"
            image="/static/images/cards/contemplative-reptile.jpg"
            title="Contemplative Reptile"
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">
              Lizard
          </Typography>
            <Typography component="p">
              Lizards are a widespread group of squamate reptiles, with over 6,000 species, ranging
              across all continents except Antarctica
          </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions>
          <Button size="small" color="primary">
            Share
        </Button>
          <Button size="small" color="primary">
            Learn More
        </Button>
        </CardActions>
      </Card>
    )
  }
}

App.propTypes = {
  classes: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  user: state.city,
})

export default connect(mapStateToProps)(App)
